@font-face {
  font-family: 'CustomFont';
  src:
       url('../fonts/Roboto-Regular.ttf') format('truetype');
  /* Add additional font formats if necessary */
  /* Specify the correct path to the font files */
}


body {
  margin: 0;
  font-family: 'CustomFont', sans-serif;
  font-size: 0.875rem; /* Base font size */
  /* overflow: hidden; Prevent horizontal scroll */
}

.video-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: -1; /* Ensure the video is behind other content */
}


.video-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: -1; /* Ensure the video is behind other content */
}

.video-background video {
  width: 100%;
  height: 100%;
  object-fit: cover; /* Ensure the video covers the entire background */
}

.navbar-container {
  display: flex;
  justify-content: center;
  padding-top: 1rem; /* Reduced padding */
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(51, 51, 51, 0.8); /* Darker background color with transparency */
  padding: 1rem 2rem; /* Reduced padding */
  border-radius: 0.625rem;
  box-shadow: 0 0.25rem 0.5rem rgba(0, 0, 0, 0.1);
  width: 80%; /* Center the navbar by setting a width */
}

.navbar-logo {
  display: flex;
  align-items: center;
}

.navbar-logo img {
  height: 2.5rem; /* Reduced the height of the logo */
  margin-right: 0.625rem;
}

.brand-name {
  color: white; /* Changed text color to white */
  font-size: 1.2rem; /* Reduced font size */
}

.navbar-menu {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
}

.navbar-item {
  margin: 0 0.625rem; /* Reduced margin */
}

.navbar-item a {
  color: white; /* Changed text color to white */
  text-decoration: none;
  padding: 0.625rem 0.9375rem; /* Reduced padding */
  border-radius: 0.3125rem;
}

.navbar-item a:hover {
  background-color: grey;
  color: white;
}

.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: white;
  min-width: 10rem;
  box-shadow: 0 0.5rem 1rem rgba(0,0,0,0.2);
  z-index: 1;
  border-radius: 0.3125rem;
}

.dropdown-content a {
  color: black;
  padding: 0.75rem 1rem;
  text-decoration: none;
  display: block;
}

.dropdown-content a:hover {
  background-color: grey;
  color: white;
}

.dropdown:hover .dropdown-content {
  display: block;
}

.dropdown:hover .dropbtn {
  background-color: grey;
  color: white;
}

/* Slideshow Styles */



.slideshow-container {
  position: relative;
  max-width: 80%;
  margin: 2rem auto;
  overflow: hidden;
  border-radius: 0.625rem;
  height: auto; /* Adjust height for better image visibility */
}

.slide {
  display: none;
  text-align: center;
  position: relative;
}

.slide.active {
  display: block;
}

.slide img {
  width: 100%;
  height: auto;
  max-height: 34rem; /* Adjust height for better image visibility */
  
  border-radius: 0.625rem;
}

.slide-content {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  padding: 1rem;
  border-radius: 0 0 0.625rem 0.625rem;
  text-align: center;
}

.slide-content p {
  margin: 0.5rem 0;
}

.slide-content button {
  background-color: #333;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 0.3125rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.slide-content button:hover {
  background-color: grey;
}

.prev, .next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  width: auto;
  padding: 0.625rem;
  margin-top: -1.375rem;
  color: white;
  font-weight: bold;
  font-size: 1.5rem;
  transition: 0.6s ease;
  user-select: none;
  background: none;
  border: none;
}

.next {
  right: 10px; /* Adjusted position */
}

.prev {
  left: 10px; /* Adjusted position */
}

.prev:hover, .next:hover {
  color: grey;
}

/* Responsive Design */
@media (max-width: 48rem) {
  .slideshow-container {
    height: auto; /* Adjust height for smaller screens */
  }

  .slide img {
    max-height: 20rem; /* Adjust height for smaller screens */
  }

  .slide-content {
    bottom: 0;
    padding: 0.5rem;
  }

  .slide-content p {
    margin: 0.25rem 0;
  }

  .slide-content button {
    padding: 0.25rem 0.5rem;
  }

  .prev, .next {
    font-size: 1rem;
    padding: 0.5rem;
  }
}

/* Featured Products Styles */
.featured-products {
  text-align: center;
  margin: 2rem auto; /* Reduced margin */
  max-width: 80%;
}

.featured-products h3 {
  font-size: 1.2rem; /* Reduced font size */
  margin-bottom: 1rem; /* Reduced margin */
  color: white;
}

.featured-products-container {
  display: flex;
  justify-content: space-between; /* Ensure products are in one line */
}

.featured-product {
  position: relative;
  border-radius: 0.625rem;
  margin: 0 0.3125rem; /* Less margin */
  padding: 0; /* No padding */
  text-align: center;
  width: 30%;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.featured-product:hover {
  transform: translateY(-0.625rem);
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2);
}

.featured-product img {
  width: 100%;
  height: 12.5rem; /* Reduced height for the image */
  object-fit: cover; /* Ensure the image covers the fixed height */
  border-radius: 0.625rem;
}

.featured-product-content {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent background */
  color: white;
  padding: 0.625rem; /* Reduced padding */
  border-radius: 0 0 0.625rem 0.625rem;
}

.featured-product p {
  margin: 0.3125rem 0; /* Less margin */
  color: white;
}

.featured-product button {
  background-color: #333;
  color: white;
  border: none;
  padding: 0.3125rem 0.625rem; /* Less padding */
  border-radius: 0.3125rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.featured-product button:hover {
  background-color: grey;
}

/* Products Styles */
.products-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 2rem auto; /* Reduced margin */
  max-width: 80%;
}

.product-card {
  background-color: rgba(51, 51, 51, 0.8); /* Grey background similar to navbar */
  border-radius: 0.625rem;
  box-shadow: 0 0.25rem 0.5rem rgba(0, 0, 0, 0.1);
  margin: 0.625rem;
  padding: 0.625rem; /* Reduced padding */
  text-align: center;
  width: 20%; /* Reduced width */
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.product-card:hover {
  transform: translateY(-0.625rem);
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2);
}

.product-card img {
  width: 100%;
  height: 9.375rem; /* Reduced height for the image */
  object-fit: cover; /* Ensure the image covers the fixed height */
  border-radius: 0.625rem;
  transition: transform 0.3s ease; /* Add transition for zoom effect */
}

.product-card:hover img {
  transform: scale(1.1); /* Zoom out effect */
}

.product-card-content {
  color: white;
}

.product-card-content h3 {
  margin: 0.625rem 0;
}

.product-card-content p {
  margin: 0.3125rem 0;
}

.product-card button {
  background-color: #333;
  color: white;
  border: none;
  padding: 0.3125rem 0.625rem; /* Reduced padding */
  border-radius: 0.3125rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.product-card button:hover {
  background-color: grey;
}

/* Pagination Styles */
.pagination {
  display: flex;
  justify-content: center;
  margin: 1rem 0; /* Reduced margin */
}

.pagination button {
  background-color: #333;
  color: white;
  border: none;
  padding: 0.625rem 0.9375rem; /* Reduced padding */
  margin: 0 0.3125rem;
  border-radius: 0.3125rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.pagination button:hover {
  background-color: grey;
}

.pagination button.active {
  background-color: grey;
}

/* Product Details Styles */
.product-details {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 2rem auto;
  max-width: 37.5rem; /* Adjusted width */
  background-color: rgba(51, 51, 51, 0.8); /* Grey background similar to navbar */
  border-radius: 0.625rem;
  box-shadow: 0 0.25rem 0.5rem rgba(0, 0, 0, 0.1);
  padding: 1.25rem;
}

.product-details img {
  width: 100%;
  height: auto;
  max-height: 18.75rem; /* Adjusted max height */
  object-fit: cover;
  border-radius: 0.625rem;
}

.product-details-content {
  text-align: center;
  color: white;
}

.product-details-content h3 {
  margin: 0.625rem 0;
}

.product-details-content p {
  margin: 0.3125rem 0;
}

.quantity {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0.625rem 0;
}

.quantity label {
  margin-right: 0.625rem;
}

.quantity input {
  width: 3.125rem;
  padding: 0.3125rem;
  text-align: center;
  border: none;
  border-radius: 0.3125rem;
  margin: 0 0.3125rem;
}

.quantity button {
  background-color: #333;
  color: white;
  border: none;
  padding: 0.3125rem 0.625rem;
  border-radius: 0.3125rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.quantity button:hover {
  background-color: grey;
}

.product-details-actions {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 1.25rem;
}

.product-details button {
  background-color: #333;
  color: white;
  border: none;
  padding: 0.625rem 1.25rem;
  border-radius: 0.3125rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin-left: 0.625rem; /* Space between quantity and button */
}

.product-details button:hover {
  background-color: grey;
}

/* Responsive Design */
@media (max-width: 48rem) {
  .product-details {
    width: 90%;
    padding: 0.625rem;
  }

  .product-details img {
    width: 100%;
  }

  .quantity {
    flex-direction: column;
  }

  .quantity input {
    margin: 0.3125rem 0;
  }

  .product-details-actions {
    flex-direction: column;
  }

  .product-details button {
    margin: 0.625rem 0 0 0; /* Adjust margin for responsive design */
  }
}

@media (max-width: 48rem) {
  body {
    overflow: auto; /* Enable scrolling on smaller screens */
  }

  .navbar-menu {
    flex-direction: column;
    width: 100%;
    display: none; /* Hide the menu by default */
  }

  .navbar-menu.active {
    display: flex; /* Show the menu when active */
  }

  .navbar-item {
    margin: 0.625rem 0; /* Reduced margin */
  }

  .hamburger {
    display: block;
    cursor: pointer;
  }

  .hamburger div {
    width: 1.5625rem;
    height: 0.1875rem;
    background-color: white;
    margin: 0.3125rem;
  }

  .slide img {
    height: 12.5rem; /* Adjust height for smaller screens */
  }

  .featured-products-container {
    flex-direction: column;
  }

  .featured-product {
    width: 100%; /* Full width for smaller screens */
    margin: 0.625rem 0;
  }

  .products-container {
    flex-direction: column;
  }

  .product-card {
    width: 100%; /* Full width for smaller screens */
    margin: 0.625rem 0;
  }
}

@media (min-width: 48.0625rem) {
  .hamburger {
    display: none;
  }
}


