/* Modal Styles */
.modal {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.8); /* Darker background with more opacity */
}

.modal-content {
  background-color: #444; /* Dark grey background */
  color: white; /* White text color */
  margin: auto;
  padding: 20px;
  border: none;
  width: 80%;
  max-width: 500px;
  border-radius: 0.625rem;
  box-shadow: 0 0.25rem 0.5rem rgba(0, 0, 0, 0.1);
}

.close {
  color: white; /* White color for close button */
  float: right;
  font-size: 28px;
  font-weight: bold;
  cursor: pointer;
}

.close:hover,
.close:focus {
  color: #ccc; /* Lighter grey on hover and focus */
  text-decoration: none;
}

.modal-content h2 {
  margin-top: 0;
  margin-bottom: 1rem;
}

.modal-content form {
  display: flex;
  flex-direction: column;
}

.modal-content .form-group {
  margin-bottom: 1rem;
}

.modal-content form label {
  margin-bottom: 0.5rem;
  color: white; /* White text color */
}

.modal-content form input {
  width: 100%;
  padding: 0.5rem;
  border: 1px solid #ccc;
  border-radius: 0.3125rem;
  background-color: #555; /* Darker input background */
  color: white; /* White text color */
}

.modal-content form button {
  background-color: #333; /* Dark grey button color */
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 0.3125rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.modal-content form button:hover {
  background-color: grey; /* Darker grey color on hover */
}

