    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    width: 100%;
  }

  .quantity {
    margin-bottom: 0.625rem;
    flex: 1;
    display: flex;
    justify-content: space-between;
  }

  .quantity button {
