/* Notification Styles */
.notification {
  position: fixed;
  top: 1rem;
  right: 1rem;
  background-color: #333;
  color: white;
  padding: 1rem;
  border-radius: 0.5rem;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);
  opacity: 0;
  transform: translateY(-2rem);
  transition: opacity 0.3s ease, transform 0.3s ease;
  z-index: 1000;
}

.notification.show {
  opacity: 1;
  transform: translateY(0);
}